




















































































import { Component, Vue } from "vue-property-decorator";
import api from "@/microApps/openPlatform/api";

@Component
export default class TeamAccessQualificationAudit extends Vue {
  searchTDO: any = {
    current: 1,
    query: {
      appName: "",
      status: "",
      companyName: ""
    },
    size: 10
  };
  applyList: any[] = [];
  loading: boolean = true;
  total: number = 0;
  details: any = {};

  async getApplyList(newSearch?: boolean) {
    if (newSearch) {
      this.searchTDO.current = 1;
    }
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(api.getApplyList, this.searchTDO);
      if (status) {
        this.total = data.total;
        this.applyList = data.records;
        for (let i in this.applyList) {
          this.$set(this.applyList[i], "isShow", false);
        }
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  async getApplyDetail(app: any, show?: boolean) {
    if (this.details[app.id]) {
      return (app.isShow = !app.isShow);
    } else {
      let {
        data: { data, status, message }
      } = await this.$http.get(api.showAccessDetail + "?id=" + app.id);
      if (status) {
        app.isShow = !app.isShow;
        this.$set(this.details, app.id, data);
      } else {
        this.$message.error(message);
      }
    }
  }

  handleCurrentChange(pageNum: number) {
    this.searchTDO.current = pageNum;
    this.getApplyList();
  }

  async passTheApply(apply: any) {
    this.$confirm("确认通过该申请?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(async () => {
      let {
        data: { data, status, message }
      } = await this.$http.get(api.passAccessTeam + "?id=" + apply.id);
      if (status) {
        this.$message.success("操作成功");
        this.getApplyList();
      } else {
        this.$message.error(message);
      }
    });
  }

  async rejectTheApply(apply: any) {
    this.$prompt("请输入拒绝原因", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      inputErrorMessage: "原因不能为空",
      distinguishCancelAndClose: true,
      inputValidator: value => {
        return !!value;
      }
    }).then(async (data: any) => {
      let {
        data: { status, message }
      } = await this.$http.post(api.rejectAccessTeam, {
        businessId: apply.id,
        result: data.value
      });
      if (status) {
        this.$message.success("操作成功");
        this.getApplyList();
      } else {
        this.$message.error(message);
      }
    });
  }

  created() {
    this.getApplyList();
  }
}
